@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer {
    padding: 3rem 0;
    border-bottom: .5rem solid v.$primario;
    &__grid {
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__copyright {
        text-align: center;
        font-size: 2.4rem;
        font-weight: 400;
        margin-top: 3rem;
        @include m.tablet {
            margin: 0;
        }
    }


}
