@use 'base/mixins' as m;
@use 'base/variables' as v;

.nosotros {
    padding: 5rem 0;
    &__heading {
        @include m.heading;
    }
    &__grid {
        @include m.tablet { 
            @include m.grid(3, 2rem);
         }
    }

    &__bloque {
        text-align: center;
    }

    &__imagen {
        display: inline;

    }
}